@use "@utils/variables" as v;

.wrapper {
    $radius: 20px;
    background-color: white;

    img {
        object-fit: cover;
    }

    .blogContent {
        padding: 1.2em;
        width: 50%;
        max-height: 250px;

        .date {
            margin-bottom: 1em;
            font-size: 0.9em;
            color: #8a8a8a;
        }

        .title {
            font-size: 1.2rem;
            margin-bottom: 0.5em;
        }

        .body {
            color: #8a8a8a;
            line-height: 2em;
            overflow-y: hidden;
        }

        footer {
            display: flex;
            align-items: center;
            margin-top: 2rem;

            .userImg {
                img {
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                }
            }

            .userInfo {
                padding-left: 0.8em;

                .occupation {
                    color: #8a8a8a;
                    font-size: 0.9em;
                    margin-top: 0.3em;
                }
            }
        }
    }

    .banner {
        display: flex;
        padding-block: 1em;
        background-color: #abc9e7;
        background-image: url("../../assets/blog/banner_bg.svg");
        background-position: center;
        background-size: cover;

        .bannerCard {
            display: flex;
            width: 100%;
            background-color: white;
            border-radius: $radius;
            max-height: 280px;

            .image {
                width: 50%;
                border-radius: $radius;
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
                height: inherit;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    border-radius: $radius;
                    border-top-right-radius: 0;
                    border-bottom-right-radius: 0;
                }
            }
        }
    }

    .blogsSection {
        padding-block: 5em;
        background-image: url("../../assets/blog/page_bg.svg");
        background-position: center;
        background-size: cover;

        .blogs {
            width: 100%;

            .blogRow {
                display: flex;
                justify-content: space-between;
                width: 100%;

                article {
                    cursor: pointer;
                    $size: 23em;
                    max-width: $size;
                    max-height: calc(2em + #{$size});
                    border-radius: $radius;
                    box-shadow: 1px 1px 20px rgba(1, 1, 1, 0.3);
                    margin-bottom: 5em;
                    background-color: white;

                    .image {
                        width: 100%;
                        height: 180px;

                        border-top-left-radius: $radius;
                        border-top-right-radius: $radius;

                        img {
                            width: 100%;
                            height: 100%;

                            border-top-left-radius: $radius;
                            border-top-right-radius: $radius;
                        }
                    }

                    .blogContent {
                        width: 100%;
                    }
                }
            }
        }
    }

    .gallerySection {
        header {
            margin-bottom: 1em;
        }

        .gallery {
            .slide {
                height: 350px;
                width: 100%;
                border-radius: 20px;
                position: relative;

                img {
                    width: 100%;
                    height: 100%;
                    border-radius: 20px;
                    object-fit: cover;
                }

                .title {
                    position: absolute;
                    bottom: 0.5em;
                    color: white;
                    background-color: rgba(1, 1, 1, 0.8);
                    padding: 1em;
                    text-align: center;
                    border-radius: 10px;
                    padding-inline: 2em;

                    left: 50%;
                    transform: translateX(-50%);
                }
            }
        }
    }
}

@media screen and (max-width: 1320px) {
    .wrapper {
        .blogsSection {
            .blogs {
                width: 100%;

                .blogRow {
                    article {
                        max-width: 420px;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 1050px) {
    .wrapper {
        .banner {
            .bannerCard {
                display: flex;
                flex-direction: column;
                max-height: unset;

                width: 100%;
                background-color: white;

                border-radius: 20px;
                height: 100%;

                .blogContent {
                    width: 100%;
                    max-height: unset;
                }

                .image {
                    width: 100%;
                    max-height: 250px;
                }
            }
        }

        .blogsSection {
            .blogs {
                .blogRow {
                    flex-direction: column;

                    article {
                        max-width: unset;
                        width: 100%;

                        .image {
                            max-height: 250px;
                        }
                    }
                }
            }
        }
    }
}
